.yo-stage {
}
.simple-form-wrap.yo-modal {
    background: linear-gradient(to right, #9fb4e7, #758ecd);
    background-color: #758ecd;
    position: fixed;
    width: 100%;
    min-height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-y: auto;
    z-index:9999999;
}

.yo-modal .simple-form {
    background-color: #fff;
    border-radius: 0.2875rem;
    border: 1px solid #fcfcfc;
    box-shadow: -1px 3px 10px 0 rgba(0, 0, 0, 0.06);
    margin: 200px auto;
    position: relative;
    transition: all 0.5s;
    width: 90%;
    max-width: 510px;
}

.simple-form > .field,
.simple-form > div > .field,
.simple-form .submit-wrap {
    padding: 1em 30px;
}

.simple-form > h1:first-child {
    margin-top: 0;
    margin-bottom: 1.5rem;
    text-align: center;
    font-size: 1.6rem;
    background: #f2f2f2;
    padding: 14px 20px;
    font-size: 1.3rem;
    font-weight: 600;
    background: #f2f2f2;
    color: #444F60 !important;
}
